
import React, { useState, useEffect } from 'react';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Textarea } from '@/components/ui/textarea';
import { Label } from '@/components/ui/label';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';
import { Plus, Search, Smartphone, Save, X, Trash2, Edit2, Phone, User, Hash, Globe, Banknote, Clock, Wifi, MessageCircle } from 'lucide-react';
import { useToast } from '@/components/ui/use-toast';
import { collection, addDoc, query, where, orderBy, onSnapshot, serverTimestamp, doc, updateDoc, deleteDoc, getDoc, writeBatch } from 'firebase/firestore';
import { db } from '@/firebase';
import { useFirebaseAuth } from '@/contexts/FirebaseAuthContext';
import { formatMoney } from '@/lib/utils';

interface SimCard {
  id: string;
  name: string;
  simNumber: string;
  lineNumber: string;
  callsPackage: string;
  internetPackage: string;
  adslPackage?: string;
  totalAmount: number;
  remainingAmount: number;
  userId: string;
  shopId?: string;
  createdAt: any;
  nationalId?: string;
  whatsappNumber?: string;
  isPaid?: boolean;
  isDeferred?: boolean;
  debtId?: string;
  notes?: string;
}

interface PaymentReceipt {
  id: string;
  cardId: string;
  cardName: string;
  amount: number;
  userId: string;
  createdAt: any;
  type: 'payment';
}

export function SimCardsDialog({ 
  isOpen, 
  onClose,
  shopId,
  onOpenAdsl
}: { 
  isOpen: boolean; 
  onClose: () => void;
  shopId?: string;
  onOpenAdsl?: () => void;
}) {
  const { user } = useFirebaseAuth();
  const { toast } = useToast();
  const [cards, setCards] = useState<SimCard[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [statusFilter, setStatusFilter] = useState<'all' | 'paid' | 'unpaid' | 'deferred'>('all');
  
  // Add/Edit Form State
  const [isFormOpen, setIsFormOpen] = useState(false);
  const [editingCard, setEditingCard] = useState<SimCard | null>(null);
  
  const [formData, setFormData] = useState({
    name: '',
    simNumber: '',
    lineNumber: '',
    callsPackage: '',
    internetPackage: '',
    adslPackage: '',
    totalAmount: '',
    remainingAmount: '',
    nationalId: '',
    whatsappNumber: '',
    notes: '',
    isPaid: false
  });

  // Deferred Debt State
  const [isDeferredDialogOpen, setIsDeferredDialogOpen] = useState(false);
  const [deferredCard, setDeferredCard] = useState<SimCard | null>(null);
  const [deferredName, setDeferredName] = useState('');
  const [deferredAmount, setDeferredAmount] = useState('');

  const [isConfirmResetOpen, setIsConfirmResetOpen] = useState(false);
  
  // Receipts State
  const [isReceiptsOpen, setIsReceiptsOpen] = useState(false);
  const [allReceipts, setAllReceipts] = useState<PaymentReceipt[]>([]);
  const [receiptSearchTerm, setReceiptSearchTerm] = useState('');
  const [receiptDateFilter, setReceiptDateFilter] = useState<string>(() => {
    const d = new Date();
    const year = d.getFullYear();
    const month = String(d.getMonth() + 1).padStart(2, '0');
    const day = String(d.getDate()).padStart(2, '0');
    return `${year}-${month}-${day}`;
  });
  const [receiptFilterType, setReceiptFilterType] = useState<'day' | 'month'>('day');

  // Fetch Cards
  useEffect(() => {
    if (!user?.uid) return;

    // Load from localStorage first
    const localKey = `simCards_${user.uid}`;
    try {
      const localData = localStorage.getItem(localKey);
      if (localData) {
        setCards(JSON.parse(localData));
        setIsLoading(false);
      }
    } catch (e) {
      console.warn("Error loading sim cards from localStorage", e);
    }

    if (!isOpen) return;

    setIsLoading(true);
    const q = query(
      collection(db, 'simCards'),
      where('userId', '==', user.uid)
    );

    const unsubscribe = onSnapshot(q, (snapshot) => {
      const data = snapshot.docs
        .map(doc => ({ id: doc.id, ...doc.data() } as SimCard))
        .sort((a, b) => {
          // Sort by createdAt desc
          const timeA = a.createdAt?.seconds || 0;
          const timeB = b.createdAt?.seconds || 0;
          return timeB - timeA;
        });
      
      console.log("Fetched SIM cards:", data);
      setCards(data);
      setIsLoading(false);
      
      // Save to localStorage
      try {
        localStorage.setItem(localKey, JSON.stringify(data));
      } catch (e) {
        console.warn("Error saving sim cards to localStorage", e);
      }
    }, (error) => {
      console.error("Error fetching sim cards:", error);
      setIsLoading(false);
    });

    return () => unsubscribe();
  }, [user?.uid, isOpen]);

  const handleOpenForm = (card?: SimCard) => {
    if (card) {
      setEditingCard(card);
      setFormData({
        name: card.name,
        simNumber: card.simNumber,
        lineNumber: card.lineNumber,
        callsPackage: card.callsPackage,
        internetPackage: card.internetPackage,
        adslPackage: card.adslPackage || '',
        totalAmount: card.totalAmount.toString(),
        remainingAmount: card.remainingAmount.toString(),
        nationalId: card.nationalId || '',
        whatsappNumber: card.whatsappNumber || '',
        notes: card.notes || '',
        isPaid: card.isPaid || false
      });
    } else {
      setEditingCard(null);
      setFormData({
        name: '',
        simNumber: '',
        lineNumber: '',
        callsPackage: '',
        internetPackage: '',
        adslPackage: '',
        totalAmount: '',
        remainingAmount: '',
        nationalId: '',
        whatsappNumber: '',
        notes: '',
        isPaid: false
      });
    }
    setIsFormOpen(true);
  };

  const handleSave = async () => {
    if (!user?.uid) return;
    
    // Validation
    if (!formData.name.trim()) {
      toast({ title: 'خطأ', description: 'يرجى إدخال الاسم', variant: 'destructive' });
      return;
    }
    if (!formData.lineNumber.trim()) {
      toast({ title: 'خطأ', description: 'يرجى إدخال رقم الخط', variant: 'destructive' });
      return;
    }

    try {
      const cardData = {
        name: formData.name,
        simNumber: formData.simNumber,
        lineNumber: formData.lineNumber,
        callsPackage: formData.callsPackage,
        internetPackage: formData.internetPackage,
        adslPackage: formData.adslPackage,
        totalAmount: parseFloat(formData.totalAmount) || 0,
        remainingAmount: parseFloat(formData.remainingAmount) || 0,
        nationalId: formData.nationalId,
        whatsappNumber: formData.whatsappNumber,
        notes: formData.notes,
        isPaid: formData.isPaid,
        userId: user.uid,
        shopId: shopId || user.uid,
        updatedAt: serverTimestamp()
      };

      if (editingCard) {
        await updateDoc(doc(db, 'simCards', editingCard.id), cardData);
        toast({ title: 'تم التحديث', description: 'تم تحديث بيانات الخط بنجاح' });
      } else {
        await addDoc(collection(db, 'simCards'), {
          ...cardData,
          createdAt: serverTimestamp()
        });
        toast({ title: 'تم الإضافة', description: 'تم إضافة الخط الجديد بنجاح' });
      }
      setIsFormOpen(false);
    } catch (error) {
      console.error(error);
      toast({ title: 'خطأ', description: 'حدث خطأ أثناء الحفظ', variant: 'destructive' });
    }
  };

  const handleDelete = async (id: string) => {
    if (!confirm('هل أنت متأكد من حذف هذا الخط؟')) return;
    try {
      await deleteDoc(doc(db, 'simCards', id));
      toast({ title: 'تم الحذف', description: 'تم حذف الخط بنجاح' });
    } catch (error) {
      console.error(error);
      toast({ title: 'خطأ', description: 'فشل الحذف', variant: 'destructive' });
    }
  };

  const handleSetPaid = async (card: SimCard, isPaid: boolean) => {
    // Optimistic UI update
    setCards(prevCards => prevCards.map(c => c.id === card.id ? { ...c, isPaid, isDeferred: false } : c));

    try {
      await updateDoc(doc(db, 'simCards', card.id), {
        isPaid: isPaid,
        isDeferred: false,
        updatedAt: serverTimestamp()
      });

      if (isPaid) {
        // Generate Receipt
        await addDoc(collection(db, 'simCardReceipts'), {
          cardId: card.id,
          cardName: card.name,
          amount: card.totalAmount,
          userId: user?.uid,
          createdAt: serverTimestamp(),
          type: 'payment'
        });
      }

      toast({ title: 'تم التحديث', description: isPaid ? 'تم تحديد الخط كمدفوع وتم تسجيل إيصال' : 'تم تحديد الخط كغير مدفوع' });
    } catch (error) {
      // Revert optimistic update on error
      setCards(prevCards => prevCards.map(c => c.id === card.id ? { ...c, isPaid: !isPaid, isDeferred: card.isDeferred } : c));
      console.error(error);
      toast({ title: 'خطأ', description: 'حدث خطأ أثناء تحديث الحالة', variant: 'destructive' });
    }
  };

  const handleOpenDeferred = (card: SimCard) => {
    setDeferredCard(card);
    setDeferredName(card.name || '');
    setDeferredAmount(card.totalAmount?.toString() || '');
    setIsDeferredDialogOpen(true);
  };

  const handleConfirmDeferred = async () => {
    if (!user?.uid || !deferredCard) return;

    if (!deferredName.trim()) {
      toast({ title: 'خطأ', description: 'يرجى إدخال اسم العميل', variant: 'destructive' });
      return;
    }
    const amount = parseFloat(deferredAmount);
    if (isNaN(amount) || amount <= 0) {
      toast({ title: 'خطأ', description: 'يرجى إدخال مبلغ صحيح', variant: 'destructive' });
      return;
    }

    try {
      const userRef = doc(db, 'users', user.uid);
      const userSnap = await getDoc(userRef);
      const userData = userSnap.data() as any;
      const currentDebts = Array.isArray(userData?.debts) ? userData.debts : [];

      const newDebt = {
        id: `DEBT-${Date.now()}`,
        debtorName: deferredName,
        mobile: deferredCard.lineNumber,
        amount: amount,
        reason: 'تمن تسديد باقة',
        status: 'pending',
        createdAt: new Date(),
        paidAmount: 0,
        partialPayments: []
      };

      await updateDoc(userRef, {
        debts: [newDebt, ...currentDebts]
      });

      // Set card as Deferred (Orange)
      setCards(prevCards => prevCards.map(c => c.id === deferredCard.id ? { ...c, isPaid: false, isDeferred: true, debtId: newDebt.id } : c));
      
      await updateDoc(doc(db, 'simCards', deferredCard.id), {
        isPaid: false,
        isDeferred: true,
        debtId: newDebt.id,
        updatedAt: serverTimestamp()
      });
      
      toast({ title: 'تم الإضافة', description: 'تم إضافة الدين وتأجيل الخط' });
      setIsDeferredDialogOpen(false);
    } catch (error) {
      console.error(error);
      toast({ title: 'خطأ', description: 'حدث خطأ أثناء حفظ الدين', variant: 'destructive' });
    }
  };

  // Listen for debt payments to auto-update deferred cards
  useEffect(() => {
    if (!user?.uid) return;
    
    const userRef = doc(db, 'users', user.uid);
    const unsubscribe = onSnapshot(userRef, async (docSnap) => {
      if (docSnap.exists()) {
        const userData = docSnap.data();
        const debts = userData.debts || [];
        
        // Find deferred cards that are not yet paid
        const deferredCards = cards.filter(c => c.isDeferred && !c.isPaid);
        
        for (const card of deferredCards) {
          let shouldUpdate = false;

          // Match by debtId if available
          if (card.debtId) {
            const debt = debts.find((d: any) => d.id === card.debtId);
            if (debt && debt.status === 'paid') {
              shouldUpdate = true;
            }
          } 
          // Fallback: Match by mobile number OR (Name + Amount) for legacy support
          else {
             const paidDebt = debts.find((d: any) => 
              d.status === 'paid' && 
              d.reason === 'تمن تسديد باقة' &&
              (
                (d.mobile && d.mobile === card.lineNumber) || 
                // Legacy match for debts created without mobile number
                (!d.mobile && d.debtorName === card.name && Math.abs(d.amount - card.totalAmount) < 0.1)
              )
            );
            if (paidDebt) {
              shouldUpdate = true;
            }
          }

          if (shouldUpdate) {
            // Update card to paid
            await handleSetPaid(card, true);
          }
        }
      }
    });
    
    return () => unsubscribe();
  }, [user?.uid, cards]);

  const filteredCards = cards.filter(c => {
    const matchesSearch = c.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
      c.lineNumber.includes(searchTerm) ||
      c.simNumber.includes(searchTerm);

    const matchesStatus = 
      statusFilter === 'all' ? true :
      statusFilter === 'paid' ? c.isPaid :
      statusFilter === 'unpaid' ? (!c.isPaid && !c.isDeferred) :
      statusFilter === 'deferred' ? c.isDeferred : true;

    return matchesSearch && matchesStatus;
  });

  const paidCount = cards.filter(c => c.isPaid).length;
  const unpaidCount = cards.filter(c => !c.isPaid && !c.isDeferred).length;
  const deferredCount = cards.filter(c => c.isDeferred).length;

  const handleResetAllUnpaid = async () => {
    setIsConfirmResetOpen(true);
  };

  const confirmResetAll = async () => {
    if (!user?.uid) return;

    try {
      const batch = writeBatch(db);
      // Iterate over all cards (using the main cards state which contains all fetched cards)
      cards.forEach(card => {
        if (card.isPaid) {
          const cardRef = doc(db, 'simCards', card.id);
          batch.update(cardRef, { 
            isPaid: false,
            updatedAt: serverTimestamp() 
          });
        }
      });

      await batch.commit();
      
      // Optimistic update
      setCards(prevCards => prevCards.map(c => ({ ...c, isPaid: false })));
      
      toast({ title: 'تم التحديث', description: 'تم تحويل جميع الخطوط إلى غير مدفوع بنجاح' });
      setIsConfirmResetOpen(false);
    } catch (error) {
      console.error("Error resetting cards:", error);
      toast({ title: 'خطأ', description: 'حدث خطأ أثناء تحديث الخطوط', variant: 'destructive' });
      setIsConfirmResetOpen(false);
    }
  };

  // Fetch All Receipts (Once when dialog opens)
  useEffect(() => {
    if (!isReceiptsOpen || !user?.uid) return;

    const q = query(
      collection(db, 'simCardReceipts'),
      where('userId', '==', user.uid)
    );

    const unsubscribe = onSnapshot(q, (snapshot) => {
      const data = snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() } as PaymentReceipt));
      // Sort desc by default
      data.sort((a, b) => {
        const timeA = a.createdAt?.seconds || Math.floor(Date.now() / 1000);
        const timeB = b.createdAt?.seconds || Math.floor(Date.now() / 1000);
        return timeB - timeA;
      });
      setAllReceipts(data);
    }, (error) => {
      console.error("Error fetching receipts:", error);
      toast({ title: 'خطأ', description: 'فشل تحميل السجل', variant: 'destructive' });
    });

    return () => unsubscribe();
  }, [isReceiptsOpen, user?.uid]);

  // Derived Filtered Receipts
  const receipts = allReceipts.filter(item => {
    // 1. Search Filter (Priority)
    if (receiptSearchTerm.trim()) {
      const term = receiptSearchTerm.toLowerCase();
      const card = cards.find(c => c.id === item.cardId);
      const nameMatch = item.cardName.toLowerCase().includes(term);
      const numberMatch = card ? (card.lineNumber.includes(term) || card.simNumber.includes(term)) : false;
      return nameMatch || numberMatch;
    }

    // 2. Date Filter (If no search)
    // If createdAt is missing (pending write), treat as current time
    const timestamp = item.createdAt?.seconds ? item.createdAt.seconds * 1000 : Date.now();
    const itemDate = new Date(timestamp);
    
    // Parse date safely as local time
    const [y, m, d] = receiptDateFilter.split('-').map(Number);
    const start = new Date(y, m - 1, d);
    const end = new Date(y, m - 1, d);

    if (receiptFilterType === 'day') {
      start.setHours(0, 0, 0, 0);
      end.setHours(23, 59, 59, 999);
    } else {
      start.setDate(1);
      start.setHours(0, 0, 0, 0);
      end.setMonth(end.getMonth() + 1);
      end.setDate(0);
      end.setHours(23, 59, 59, 999);
    }

    return itemDate >= start && itemDate <= end;
  });

  return (
    <>
      <Dialog open={isOpen} onOpenChange={(open) => !open && onClose()}>
        <DialogContent className="max-w-screen w-screen h-screen flex flex-col p-0 gap-0 bg-gray-50 dark:bg-gray-900 rounded-none border-0">
          {/* Header */}
          <div className="p-3 border-b bg-white dark:bg-gray-800 flex items-center justify-between">
            <div className="flex items-center gap-2">
              <div className="p-1.5 bg-purple-100 dark:bg-purple-900/30 rounded-full text-purple-600">
                <Smartphone className="h-5 w-5" />
              </div>
              <div className="flex items-center gap-2">
                <DialogTitle className="text-base font-bold">إدارة الخطوط</DialogTitle>
                <DialogDescription className="text-xs">سجل ومتابعة خطوط الموبايل والباقات</DialogDescription>
              </div>
            </div>
            <Button onClick={() => onClose()} variant="ghost" size="icon" className="h-8 w-8">
              <X className="h-4 w-4" />
            </Button>
          </div>

          {/* Status Filters */}
          <div className="grid grid-cols-3 gap-2 p-2 bg-gray-50/50 dark:bg-gray-900/50 border-b">
            {/* Paid Card */}
            <div 
              onClick={() => setStatusFilter(statusFilter === 'paid' ? 'all' : 'paid')}
              className={`cursor-pointer rounded-md border py-1.5 px-2 flex flex-row items-center justify-center gap-2 transition-all hover:shadow-sm ${
                statusFilter === 'paid' 
                  ? 'bg-green-100 border-green-300 ring-1 ring-green-500 ring-offset-1 dark:ring-offset-gray-900' 
                  : 'bg-white dark:bg-gray-800 border-gray-200 dark:border-gray-700 hover:border-green-300'
              }`}
            >
              <div className={`p-1 rounded-full ${statusFilter === 'paid' ? 'bg-green-200' : 'bg-green-100'} text-green-700`}>
                <Banknote className="h-3.5 w-3.5" />
              </div>
              <div className="flex items-center gap-1.5 leading-none">
                <div className="text-[10px] font-bold text-gray-600 dark:text-gray-400">المدفوعة</div>
                <div className="text-sm font-bold text-green-600">{paidCount}</div>
              </div>
            </div>

            {/* Unpaid Card */}
            <div 
              onClick={() => setStatusFilter(statusFilter === 'unpaid' ? 'all' : 'unpaid')}
              className={`cursor-pointer rounded-md border py-1.5 px-2 flex flex-row items-center justify-center gap-2 transition-all hover:shadow-sm ${
                statusFilter === 'unpaid' 
                  ? 'bg-red-100 border-red-300 ring-1 ring-red-500 ring-offset-1 dark:ring-offset-gray-900' 
                  : 'bg-white dark:bg-gray-800 border-gray-200 dark:border-gray-700 hover:border-red-300'
              }`}
            >
              <div className={`p-1 rounded-full ${statusFilter === 'unpaid' ? 'bg-red-200' : 'bg-red-100'} text-red-700`}>
                <Clock className="h-3.5 w-3.5" />
              </div>
              <div className="flex items-center gap-1.5 leading-none">
                <div className="text-[10px] font-bold text-gray-600 dark:text-gray-400">غير المدفوعة</div>
                <div className="text-sm font-bold text-red-600">{unpaidCount}</div>
              </div>
            </div>

            {/* Deferred Card */}
            <div 
              onClick={() => setStatusFilter(statusFilter === 'deferred' ? 'all' : 'deferred')}
              className={`cursor-pointer rounded-md border py-1.5 px-2 flex flex-row items-center justify-center gap-2 transition-all hover:shadow-sm ${
                statusFilter === 'deferred' 
                  ? 'bg-orange-100 border-orange-300 ring-1 ring-orange-500 ring-offset-1 dark:ring-offset-gray-900' 
                  : 'bg-white dark:bg-gray-800 border-gray-200 dark:border-gray-700 hover:border-orange-300'
              }`}
            >
              <div className={`p-1 rounded-full ${statusFilter === 'deferred' ? 'bg-orange-200' : 'bg-orange-100'} text-orange-700`}>
                <Clock className="h-3.5 w-3.5" />
              </div>
              <div className="flex items-center gap-1.5 leading-none">
                <div className="text-[10px] font-bold text-gray-600 dark:text-gray-400">مؤجلة الدفع</div>
                <div className="text-sm font-bold text-orange-600">{deferredCount}</div>
              </div>
            </div>
          </div>

          {/* Actions & Search */}
          <div className="p-4 bg-white dark:bg-gray-800 border-b flex flex-col md:flex-row gap-3 justify-between items-center">
            <div className="relative w-full md:w-64">
              <Search className="absolute right-3 top-1/2 -translate-y-1/2 h-4 w-4 text-gray-400" />
              <Input 
                placeholder="بحث بالاسم أو الرقم..." 
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="pr-9"
              />
            </div>
            <div className="flex gap-2 w-full md:w-auto">
              {onOpenAdsl && (
                <Button onClick={onOpenAdsl} variant="outline" className="flex-1 md:flex-none gap-2 text-blue-600 border-blue-200 hover:bg-blue-50">
                   <Wifi className="h-4 w-4" />
                   ADSL
                </Button>
              )}
              <Button onClick={() => setIsReceiptsOpen(true)} variant="outline" className="flex-1 md:flex-none gap-2">
                 <Banknote className="h-4 w-4" />
                 سجل السداد
              </Button>
              <Button onClick={() => handleOpenForm()} className="flex-1 md:flex-none bg-purple-600 hover:bg-purple-700 text-white gap-2">
                <Plus className="h-4 w-4" />
                إضافة خط جديد
              </Button>
              <Button 
                onClick={handleResetAllUnpaid} 
                variant="destructive"
                className="flex-1 md:flex-none gap-2"
              >
                <Clock className="h-4 w-4" />
                تحويل الكل لغير مدفوع
              </Button>
            </div>
          </div>

          {/* Content */}
          <div className="flex-1 overflow-auto p-4">
            {isLoading ? (
              <div className="flex justify-center items-center h-40">
                <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-purple-600"></div>
              </div>
            ) : filteredCards.length === 0 ? (
              <div className="flex flex-col items-center justify-center h-64 text-gray-500">
                <Smartphone className="h-12 w-12 mb-3 opacity-20" />
                <p>لا توجد خطوط مسجلة</p>
                <Button variant="link" onClick={() => handleOpenForm()} className="text-purple-600">
                  إضافة أول خط
                </Button>
              </div>
            ) : (
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-3">
                {filteredCards.map(card => (
                  <div key={card.id} className={`rounded-xl shadow-sm border p-2 hover:shadow-md transition-shadow ${
                    card.isDeferred
                      ? 'bg-orange-50 dark:bg-orange-900/20 border-orange-200 dark:border-orange-800'
                      : card.isPaid 
                        ? 'bg-green-50 dark:bg-green-900/20 border-green-200 dark:border-green-800' 
                        : 'bg-red-50 dark:bg-red-900/20 border-red-200 dark:border-red-800'
                  }`}>
                    <div className="flex justify-between items-start mb-1.5">
                      <div className="flex items-center gap-2">
                        <div className={`h-8 w-8 rounded-full flex items-center justify-center font-bold text-sm ${
                          card.isDeferred
                            ? 'bg-orange-100 text-orange-700 dark:bg-orange-800 dark:text-orange-100'
                            : card.isPaid 
                              ? 'bg-green-100 text-green-700 dark:bg-green-800 dark:text-green-100' 
                              : 'bg-red-100 text-red-700 dark:bg-red-800 dark:text-red-100'
                        }`}>
                          {card.name.charAt(0)}
                        </div>
                        <div>
                          <h3 className="font-bold text-gray-900 dark:text-gray-100 text-sm">{card.name}</h3>
                          <div className="flex items-center gap-1 text-[10px] text-gray-500">
                            <Phone className="h-3 w-3" />
                            <span dir="ltr">{card.lineNumber}</span>
                          </div>
                        </div>
                      </div>
                      <div className="flex gap-1">
                        <Button variant="ghost" size="icon" className="h-6 w-6 text-blue-600 hover:bg-blue-50" onClick={() => handleOpenForm(card)}>
                          <Edit2 className="h-3 w-3" />
                        </Button>
                        <Button variant="ghost" size="icon" className="h-6 w-6 text-red-600 hover:bg-red-50" onClick={() => handleDelete(card.id)}>
                          <Trash2 className="h-3 w-3" />
                        </Button>
                      </div>
                    </div>
                    
                    <div className="space-y-1 text-xs">
                      {card.nationalId && (
                        <div className="flex justify-between py-0.5 border-b border-gray-200/50">
                          <span className="text-gray-500 flex items-center gap-1"><User className="h-3 w-3" /> الرقم القومي</span>
                          <span className="font-medium font-mono">{card.nationalId}</span>
                        </div>
                      )}
                      <div className="flex justify-between py-0.5 border-b border-gray-200/50">
                        <span className="text-gray-500 flex items-center gap-1"><Hash className="h-3 w-3" /> رقم الموزع</span>
                        <span className="font-medium font-mono">{card.simNumber || '-'}</span>
                      </div>
                      <div className="flex justify-between py-0.5 border-b border-gray-200/50">
                        <span className="text-gray-500 flex items-center gap-1"><Phone className="h-3 w-3" /> باقة المكالمات</span>
                        <span className="font-medium">{card.callsPackage || '-'}</span>
                      </div>
                      <div className="flex justify-between py-0.5 border-b border-gray-200/50">
                        <span className="text-gray-500 flex items-center gap-1"><Globe className="h-3 w-3" /> باقة النت</span>
                        <span className="font-medium">{card.internetPackage || '-'}</span>
                      </div>
                      {card.adslPackage && (
                        <div className="flex justify-between py-0.5 border-b border-gray-200/50">
                          <span className="text-gray-500 flex items-center gap-1"><Wifi className="h-3 w-3" /> باقة ADSL</span>
                          <span className="font-medium">{card.adslPackage}</span>
                        </div>
                      )}
                      <div className="flex justify-between py-1 pt-1.5">
                        <span className="text-gray-500 flex items-center gap-1"><Banknote className="h-3 w-3" /> الإجمالي</span>
                        <span className="font-bold text-gray-900">{formatMoney(card.totalAmount)}</span>
                      </div>
                      
                      {card.whatsappNumber && (
                        <div className="mt-2 px-1">
                          <Button 
                            variant="outline" 
                            className="w-full h-8 gap-2 text-green-600 border-green-200 hover:bg-green-50 hover:text-green-700"
                            onClick={() => {
                              const message = `مرحباً ${card.name}،\nتذكير بموعد دفع فاتورة الخط رقم ${card.lineNumber}.\nالمبلغ المطلوب: ${card.totalAmount} جنيه.\nشكراً لك!`;
                              const url = `https://wa.me/2${card.whatsappNumber}?text=${encodeURIComponent(message)}`;
                              window.open(url, '_blank');
                            }}
                          >
                            <MessageCircle className="h-4 w-4" />
                            تذكير بالدفع
                          </Button>
                        </div>
                      )}

                      {/* {card.remainingAmount > 0 && (
                         <div className="flex justify-between py-0.5 bg-white/50 px-2 rounded-md">
                           <span className="text-red-600 flex items-center gap-1"><Clock className="h-3 w-3" /> المتبقي</span>
                           <span className="font-bold text-red-600">{formatMoney(card.remainingAmount)}</span>
                         </div>
                      )} */}
                      <div className="flex gap-2 mt-1.5 px-1">
                        <Button 
                          size="sm"
                          className={`flex-1 h-8 text-xs font-bold ${
                            card.isPaid 
                              ? 'bg-green-600 hover:bg-green-700 text-white' 
                              : 'bg-white text-green-600 border border-green-200 hover:bg-green-50'
                          }`}
                          onClick={() => handleSetPaid(card, true)}
                        >
                          مدفوع
                        </Button>
                        <Button 
                          size="sm"
                          variant="destructive"
                          className={`flex-1 h-8 text-xs font-bold ${
                            !card.isPaid && !card.isDeferred
                              ? 'bg-red-600 hover:bg-red-700 text-white' 
                              : 'bg-white text-red-600 border border-red-200 hover:bg-red-50'
                          }`}
                          onClick={() => handleSetPaid(card, false)}
                        >
                          غير مدفوع
                        </Button>
                        <Button 
                          size="sm"
                          variant="outline"
                          className={`flex-1 h-8 text-xs font-bold ${
                            card.isDeferred
                              ? 'bg-orange-600 hover:bg-orange-700 text-white'
                              : 'text-orange-600 border-orange-200 hover:bg-orange-50'
                          }`}
                          onClick={() => handleOpenDeferred(card)}
                        >
                          أجل
                        </Button>
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            )}
          </div>
        </DialogContent>
      </Dialog>

      {/* Add/Edit Form Dialog */}
      <Dialog open={isReceiptsOpen} onOpenChange={setIsReceiptsOpen}>
        <DialogContent className="max-w-2xl h-[80vh] flex flex-col p-0">
          <DialogHeader className="p-4 border-b">
            <DialogTitle>سجل سداد الخطوط</DialogTitle>
            <DialogDescription>
              عرض إيصالات السداد حسب التاريخ
            </DialogDescription>
          </DialogHeader>
          
          <div className="p-4 border-b bg-gray-50 flex flex-col gap-4">
            {/* Search Input */}
            <div className="relative w-full">
              <Search className="absolute right-3 top-1/2 -translate-y-1/2 h-4 w-4 text-gray-400" />
              <Input 
                placeholder="بحث في السجل بالاسم أو الرقم..." 
                value={receiptSearchTerm}
                onChange={(e) => setReceiptSearchTerm(e.target.value)}
                className="pr-9 bg-white"
              />
            </div>

            <div className="flex flex-col sm:flex-row gap-4 items-end w-full">
              <div className="space-y-2 flex-1">
                <Label>نوع البحث</Label>
                <div className="flex bg-white rounded-md border p-1">
                  <button
                    onClick={() => setReceiptFilterType('day')}
                    className={`flex-1 px-3 py-1.5 text-sm rounded-sm transition-colors ${
                      receiptFilterType === 'day' ? 'bg-purple-100 text-purple-700 font-medium' : 'text-gray-600 hover:bg-gray-50'
                    }`}
                  >
                    يومي
                  </button>
                  <button
                    onClick={() => setReceiptFilterType('month')}
                    className={`flex-1 px-3 py-1.5 text-sm rounded-sm transition-colors ${
                      receiptFilterType === 'month' ? 'bg-purple-100 text-purple-700 font-medium' : 'text-gray-600 hover:bg-gray-50'
                    }`}
                  >
                    شهري
                  </button>
                </div>
              </div>
              
              <div className="space-y-2 flex-1">
                <Label>{receiptFilterType === 'day' ? 'اختر اليوم' : 'اختر الشهر'}</Label>
                <Input
                  type={receiptFilterType === 'day' ? 'date' : 'month'}
                  value={receiptFilterType === 'day' ? receiptDateFilter : receiptDateFilter.substring(0, 7)}
                  onChange={(e) => setReceiptDateFilter(e.target.value)}
                  className="bg-white"
                />
              </div>

              <div className="space-y-2">
                 <div className="text-sm font-medium text-gray-500 mb-1">إجمالي الفترة</div>
                 <div className="text-lg font-bold text-green-600">
                   {formatMoney(receipts.reduce((sum, r) => sum + (r.amount || 0), 0))}
                 </div>
              </div>
            </div>
          </div>

          <div className="flex-1 overflow-y-auto p-0 relative">
            {receipts.length === 0 ? (
               <div className="flex flex-col items-center justify-center h-48 text-gray-500">
                 <Banknote className="h-12 w-12 mb-2 opacity-20" />
                 <p>لا توجد عمليات سداد في هذه الفترة</p>
               </div>
            ) : (
              <Table>
                <TableHeader className="sticky top-0 bg-white z-10 shadow-sm">
                  <TableRow>
                    <TableHead className="text-right">التاريخ</TableHead>
                    <TableHead className="text-right">اسم الخط</TableHead>
                    <TableHead className="text-right">المبلغ</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {receipts.map((receipt) => (
                    <TableRow key={receipt.id}>
                      <TableCell className="font-mono text-xs text-gray-500">
                        {receipt.createdAt?.seconds 
                          ? new Date(receipt.createdAt.seconds * 1000).toLocaleString('ar-EG') 
                          : 'الآن'}
                      </TableCell>
                      <TableCell className="font-medium">{receipt.cardName}</TableCell>
                      <TableCell className="font-bold text-green-600">{formatMoney(receipt.amount)}</TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            )}
          </div>
        </DialogContent>
      </Dialog>

      <Dialog open={isConfirmResetOpen} onOpenChange={setIsConfirmResetOpen}>
        <DialogContent className="max-w-md">
          <DialogHeader>
            <DialogTitle>تأكيد التحويل</DialogTitle>
            <DialogDescription>
              هل أنت متأكد من تحويل جميع الخطوط إلى "غير مدفوع"؟
              <br />
              سيتم تغيير حالة الدفع لجميع الخطوط المسجلة.
            </DialogDescription>
          </DialogHeader>
          <div className="flex justify-end gap-2 mt-4">
            <Button variant="outline" onClick={() => setIsConfirmResetOpen(false)}>
              إلغاء
            </Button>
            <Button variant="destructive" onClick={confirmResetAll}>
              نعم، تحويل الكل
            </Button>
          </div>
        </DialogContent>
      </Dialog>

      <Dialog open={isFormOpen} onOpenChange={setIsFormOpen}>
        <DialogContent className="max-w-md">
          <DialogHeader>
            <DialogTitle>{editingCard ? 'تعديل بيانات الخط' : 'إضافة خط جديد'}</DialogTitle>
          </DialogHeader>
          <div className="space-y-4 py-2">
            <div className="space-y-2">
              <Label>الاسم</Label>
              <Input 
                value={formData.name} 
                onChange={(e) => setFormData({...formData, name: e.target.value})}
                placeholder="اسم العميل أو صاحب الخط"
              />
            </div>
            
            <div className="space-y-2">
              <Label>الرقم القومي (اختياري)</Label>
              <Input 
                value={formData.nationalId} 
                onChange={(e) => setFormData({...formData, nationalId: e.target.value})}
                placeholder="الرقم القومي..."
                dir="ltr"
              />
            </div>

            <div className="grid grid-cols-2 gap-4">
              <div className="space-y-2">
                <Label>رقم الخط</Label>
                <Input 
                  value={formData.lineNumber} 
                  onChange={(e) => setFormData({...formData, lineNumber: e.target.value})}
                  placeholder="01xxxxxxxxx"
                  dir="ltr"
                />
              </div>
              <div className="space-y-2">
                <Label>رقم الموزع</Label>
                <Input 
                  value={formData.simNumber} 
                  onChange={(e) => setFormData({...formData, simNumber: e.target.value})}
                  placeholder="رقم الموزع..."
                />
              </div>
            </div>

            <div className="space-y-2">
              <Label>رقم الواتساب (اختياري)</Label>
              <Input 
                value={formData.whatsappNumber} 
                onChange={(e) => setFormData({...formData, whatsappNumber: e.target.value})}
                placeholder="رقم الواتساب..."
                dir="ltr"
              />
            </div>

            <div className="grid grid-cols-2 gap-4">
              <div className="space-y-2">
                <Label>باقة المكالمات</Label>
                <Input 
                  value={formData.callsPackage} 
                  onChange={(e) => setFormData({...formData, callsPackage: e.target.value})}
                  placeholder="مثال: فليكس 45"
                />
              </div>
              <div className="space-y-2">
                <Label>باقة النت</Label>
                <Input 
                  value={formData.internetPackage} 
                  onChange={(e) => setFormData({...formData, internetPackage: e.target.value})}
                  placeholder="مثال: سوبر ميجا 20"
                />
              </div>
            </div>

            <div className="space-y-2">
              <Label>باقة ADSL (اختياري)</Label>
              <Input 
                value={formData.adslPackage} 
                onChange={(e) => setFormData({...formData, adslPackage: e.target.value})}
                placeholder="مثال: 140 جيجا"
              />
            </div>

            <div className="space-y-2">
              <Label>ملاحظات (اختياري)</Label>
              <Textarea 
                value={formData.notes} 
                onChange={(e) => setFormData({...formData, notes: e.target.value})}
                placeholder="أي ملاحظات إضافية..."
                className="resize-none"
              />
            </div>

            <div className="grid grid-cols-1 gap-4">
              <div className="space-y-2">
                <Label>المبلغ الإجمالي</Label>
                <Input 
                  type="number"
                  value={formData.totalAmount} 
                  onChange={(e) => setFormData({...formData, totalAmount: e.target.value})}
                  placeholder="0.00"
                />
              </div>
              {/* <div className="space-y-2">
                <Label>المبلغ المتبقي</Label>
                <Input 
                  type="number"
                  value={formData.remainingAmount} 
                  onChange={(e) => setFormData({...formData, remainingAmount: e.target.value})}
                  placeholder="0.00"
                />
              </div> */}
            </div>

            <div className="space-y-2 pt-2 hidden">
              <Label>حالة الدفع</Label>
              <div className="flex gap-2">
                <Button 
                  type="button" 
                  variant={formData.isPaid ? "default" : "outline"}
                  className={`flex-1 ${formData.isPaid ? "bg-green-600 hover:bg-green-700" : "text-green-600 border-green-200 hover:bg-green-50"}`}
                  onClick={() => setFormData({...formData, isPaid: true})}
                >
                  مدفوع
                </Button>
                <Button 
                  type="button"
                  variant={!formData.isPaid ? "default" : "outline"}
                  className={`flex-1 ${!formData.isPaid ? "bg-red-600 hover:bg-red-700" : "text-red-600 border-red-200 hover:bg-red-50"}`}
                  onClick={() => setFormData({...formData, isPaid: false})}
                >
                  لم يتم الدفع
                </Button>
              </div>
            </div>

            <div className="pt-4 flex gap-2">
              <Button className="flex-1 bg-purple-600 hover:bg-purple-700 text-white" onClick={handleSave}>
                <Save className="h-4 w-4 ml-2" />
                حفظ
              </Button>
              <Button variant="outline" onClick={() => setIsFormOpen(false)}>إلغاء</Button>
            </div>
          </div>
        </DialogContent>
      </Dialog>

      {/* Deferred Debt Dialog */}
      <Dialog open={isDeferredDialogOpen} onOpenChange={setIsDeferredDialogOpen}>
        <DialogContent className="max-w-sm">
          <DialogHeader>
            <DialogTitle>تسجيل معاملة آجل</DialogTitle>
            <DialogDescription>سيتم إضافة المبلغ للديون المستحقة</DialogDescription>
          </DialogHeader>
          <div className="space-y-4 py-2">
            <div className="space-y-2">
              <Label>اسم العميل</Label>
              <Input 
                value={deferredName} 
                onChange={(e) => setDeferredName(e.target.value)}
                placeholder="اسم العميل..."
              />
            </div>
            <div className="space-y-2">
              <Label>المبلغ</Label>
              <Input 
                type="number"
                value={deferredAmount} 
                onChange={(e) => setDeferredAmount(e.target.value)}
                placeholder="0.00"
              />
            </div>
            <div className="pt-2 flex gap-2">
              <Button className="flex-1 bg-orange-600 hover:bg-orange-700 text-white" onClick={handleConfirmDeferred}>
                تأكيد الأجل
              </Button>
              <Button variant="outline" onClick={() => setIsDeferredDialogOpen(false)}>إلغاء</Button>
            </div>
          </div>
        </DialogContent>
      </Dialog>
    </>
  );
}
